<template>
  <li>
    <div class="container">
      <div v-show="!visible" class="tip-container">
        <img
          src="../../assets/images/system-tooltip/arrow-list.svg"
          alt="arrow"
          class="arrow"
          v-on:click="visible = !visible"
        />
        <div class="container_title">
          <div class="title">
            {{ tip.title }}
            <!-- <HumanCreated v-if="tip.is_human_created"/> -->
            <!--            <div class="tooltip tooltip_down" v-if="tip.is_human_created">
              <img
                src="../../assets/images/IconHumanCreated/IconHuman.svg"
                alt="Human created"
                class="tooltip__element"
              />
              <span class="tooltip__info">Сделано экспертом</span>
            </div>
-->
          </div>
        </div>
        <p class="fs-medium">{{ tip.category }}</p>
      </div>
      <div class="tip-container" v-show="visible">
        <img
          src="../../assets/images/system-tooltip/arrow-list.svg"
          alt="arrow"
          class="arrow rotate-arrow"
          v-on:click="visible = !visible"
        />
        <div class="container_title">
          <div class="title">
            {{ tip.title }}
            <!-- <HumanCreated :data="tip.is_human_created"/> -->
            <!--            <div class="tooltip tooltip_down" v-if="tip.is_human_created">
              <img
                src="../../assets/images/IconHumanCreated/IconHuman.svg"
                alt="Human created"
                class="tooltip__element"
              />
              <span class="tooltip__info">Сделано экспертом</span>
            </div>
-->
          </div>
        </div>
        <p class="des">Категория: {{ tip.category }}</p>
        <p class="fs-medium">{{ tip.description }}</p>
      </div>
      <div class="date">
        <div class="dtop">{{ tip.created_at }}</div>
        <!-- <div class="dbot">{{ tip.date.time }}</div> -->
      </div>
      <button
        class="rem"
        @click="
          removed
            ? CHANGE_REMOVED({ id: tip.id, token, type: 'Tip' })
            : CHANGE_RECENT({ id: tip.id, token, type: 'Tip' })
        "
      >
        <img
          v-if="removed"
          src="@/assets/images/alerts/btn-undo.svg"
          alt="remove tip"
        />
        <img
          v-if="!removed"
          src="@/assets/images/alerts/btn-removed.svg"
          alt="remove tip"
        />
      </button>
    </div>
  </li>
</template>

<script>
import { mapMutations } from "vuex";
// import HumanCreated from "../humanCreated/HumanCreated"
export default {
  props: ["tip", "token", "removed"],
  // components: {
  //   HumanCreated
  // },
  data() {
    return {
      visible: false
    };
  },
  computed: {},
  methods: {
    ...mapMutations("Tip", ["CHANGE_RECENT", "CHANGE_DONE", "CHANGE_REMOVED"])
  },
  mounted() {
    // console.log(this.tip);
  }
};
</script>

<style lang="scss" scoped>
.container {
  position: relative;
  display: flex;
  margin-bottom: 1rem;
}
.tip-container {
  width: 50vw;
  background: #262626;
  border-radius: 1rem;
  font-size: 3rem;
  padding: 0.7rem 1.2rem 0.7rem 1.2rem;
  color: white;
}
.title {
  // display: block;
  font-family: "GilroyLight";
  color: #7873ff;
  text-align: justify;
}
.des {
  font-family: "GilroyLight";
  text-align: justify;
}
.fs-medium {
  font-size: 2.5rem;
  text-align: justify;
}
.rotate-arrow {
  transform: rotate(180deg);
}
.arrow {
  margin: 0.9rem 0 0 1.2rem;
  float: right;
  width: 4rem;
  cursor: pointer;
}
.date {
  width: 10rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 1rem;
  padding: 0.5rem 0 0 0;
  color: white;
}
.dbot {
  font-size: 3rem;
}
.rem {
  margin: 0.5rem 0 0 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background: #0b0b0c;
  border: none;
  outline: none;
  width: 5.5rem;

  img {
    width: 3.5rem;
  }
}
.container_title {
  display: flex;
}
.tooltip {
  margin: 1rem 0 0 1rem;
  display: inline-block;
  // float: right;
  position: relative;
  cursor: pointer;
}
.tooltip:hover .tooltip__element {
  filter: brightness(1.5);
}
.tooltip:hover .tooltip__info {
  display: block;
}
.tooltip__info {
  display: none;
  position: absolute;
  left: -190%;
  top: 110%;
  padding: 5px 10px;
  font-size: 0.8em;
  text-align: center;
  color: #fff;
  background-color: #0b0b0c;
  z-index: 1;
  border-radius: 0 0 10px 10px;
  box-shadow: 0px 1px 5px 1px rgb(255, 255, 255, 0.5);
}
.tooltip__info::after {
  content: "";
  position: absolute;
  border: 0 solid transparent;
}
.tooltip__info::before {
  content: "";
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
}
.tooltip_down .tooltip__info::after {
  left: 50%;
  transform: translate(-50%, 0);
  bottom: 100%;
  border-width: 0 7rem 1rem 7rem;
  border-bottom-color: #0b0b0c;
}
.tooltip__info .tooltip_down {
  left: 50%;
  transform: translate(0, -50%);
  top: 100%;
  margin-top: 5px;
}
</style>
